.menu {
    display: none;
    
    color: $primary-title-color-dk;
    font-weight: 600;
    font-size: 18px;
    line-height: 1.4;
    letter-spacing: 0.1em;

    @media screen and (min-width: 768px) {
    display: block;
    margin-right: 24px;

    color: $primary-title-color-lt;
    font-size: 12px;
    line-height: 1.33;
    margin-left: 168px;
    }
}

.menu-wrap {
    @media screen and (max-width: 479px) {
        min-width: 285px;
        max-width: 70%;
        flex-grow: 1;
    }

    @media screen and (max-width: 767px) {
        margin-left: auto;
        padding-top: 48px;
        padding-left: 40px;
        padding-right: 31px;
        height: 100%;
        position: relative;
        overflow-y: scroll;
        background-color: $background-color-lt;
    }
    @media screen and (min-width: 480px) and (max-width: 767px) {
        max-width: 337px;
    }

    @media screen and (min-width: 768px) {
        display: flex;
    }

    @media screen and (min-width: 1200px) {
        align-items: center;
    }
}

.menu-box {
    @media screen and (max-width: 767px) {
        margin-bottom: 64px;
    }
    @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;
        margin-left: auto;
        flex-direction: column-reverse;
    }

    @media screen and (min-width: 1200px) {
        flex-direction: row;
    }

    & .button--inverse {

        @media screen and (min-width: 767px) and (max-width: 1199px) {
            margin-bottom: 20px;
        }

        @media screen and (min-width: 768px) {
            color: $primary-title-color-lt;
        }
    }
}

.tel {
    display: block;
    @media screen and (max-width: 767px) {
        margin-bottom: 24px;
    }
    @media screen and (min-width: 1200px) {
        margin-right: 40px;
    }
}

.nav {
    @media screen and (max-width: 767px) {
        margin-bottom: 64px;
    }

    &-list {
        @media screen and (min-width: 768px) {    
            display: flex;
        }
        @media screen and (max-width: 1199px) {    
            padding-top: 12px;
        }
    }
    &__item {
        display: block;
    }
    &__item:not(:last-child) {
        margin-right: 24px;

        @media screen and (min-width: 1200px) {
            margin-right: 40px;
        }
    }
    &__item:not(:last-child), .aside-media__item:not(:last-child), .tel {
        @media screen and (max-width: 767px){
            margin-bottom: 24px;
        }
    }

    &-list__link:hover, .nav-list__link:focus {
        color: $accent-color;
    }
}




.aside-media__item:first-child::before {
    content: '';
      display: inline-block;
      width: 60px;
      height: 6px;
      border-top: 1px solid $primary-title-color-dk;
      margin-right: 40px;
}

.aside-media__item:not(:first-child) {
    @media screen and (max-width: 767px){
        margin-left: 100px;
        margin-top: 24px;
        padding-bottom: 60px;
    }
    @media screen and (min-width: 768px) {
        margin-left: 40px;
    }
}

.aside-media {
    @media screen and (min-width: 768px){
        position: absolute;
        bottom: 264px;
        left: -264px;
        rotate: 270deg;
        display: flex;
        padding-top: 76px;
        width: 688px;
        height: 159px;

        color: #303030;
        background-color: $background-color-lt;
    }
}